  max-width: 1000px;
  position: relative; }

.category__slider__title-bg {
  color: #3A7498;
  font-family: "Poppins", monospace;
  font-size: 10rem;
  opacity: 0.03;
  position: absolute;
  top: -50px; }

.single-product__headline {
  display: grid;
  grid-template-columns: 1fr 1fr; }
  @media (min-width: 768px) {
    .single-product__headline {
      flex-direction: row;
      gap: 50px;
      margin: 160px auto 0;
      max-width: 1500px;
      padding: 24px; } }

.single-product__headline__wrapper-image {
  max-width: 800px; }

.single-product__headline__image {
  width: 100%; }

@media (max-width: 767px) {
  .single-product__headline__information__title-container {
    padding: 0px 20px; } }

.single-product__headline__information__title {
  font-size: clamp(3.5rem, 10vw, 7rem); }

.single-product__headline__description-container {
  margin-top: 40px;
  max-width: 500px;
  font-weight: 300;
  font-family: "Poppins", monospace;
  line-height: 180%; }
  @media (max-width: 767px) {
    .single-product__headline__description-container {
      padding: 0px 20px; } }

.single-product__headline__information__title-container {
  margin-top: 32px; }

.single-product__headline__description {
  font: revert; }

.slider-container {
  display: flex;
  gap: 50px;
  overflow-x: scroll;
  width: 3000px; }
  .slider-container .slide__item {
    overflow: hidden; }
